.container {
	@include container;
}
@media (max-width: 1200px) {
	.container {
		max-width: 960px;
	}
}
@media (max-width: 992px) {
	.container {
		max-width: 720px;
	}
}
@media (max-width: 768px) {
	.container {
		max-width: 540px;
	}
}
@media (max-width: 576px) {
	.container {
		max-width: 100%;
	}
}
.why-thrive {
	display: flex;
	justify-content: space-between;
	background: #62d0df;
	&__description {
		max-width: 555px;
		padding: 150px 0 0 0;
		margin-left: 178px;
		&-title {
			font-weight: 700;
			font-size: 36px;
			line-height: 23px;
			letter-spacing: -0.006em;
			color: #ffffff;
		}
		&-text {
			margin-top: 75px;
			font-size: 20px;
			line-height: 31px;
			color: #ffffff;
			span {
				font-weight: 700;
				font-size: 24px;
				line-height: 30px;
			}
		}
	}
	&__img {
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	@media (max-width: 1200px) {
		.why-thrive {
			&__description {
				margin-left: 60px;
				padding: 100px 0 0 0;
				&-text {
					margin-top: 45px;
				}
			}
		}
	}
	@media (max-width: 992px) {
		.why-thrive {
			&__description {
				max-width: 370px;
			}
		}
	}
	@media (max-width: 768px) {
		.why-thrive {
			&__img {
				display: none;
			}
		}
	}
	@media (max-width: 576px) {
		.why-thrive {
			&__description {
				padding: 70px 10px 0 10px;
			}
		}
	}
}
