.footer {
	&-content {
		display: flex;
		.column-one {
			width: 570px;
			min-height: 380px;
			background: url(img/flowers.jpg) 0 0 / cover no-repeat;
			margin: 0 65px 0 0;
			@media (max-width: 1200px) {
				width: 50%;
				margin: 0;
			}
			@media (max-width: 992px) {
				display: none;
			}
		}
		.column-two {
			display: flex;
			flex-flow: row wrap;
			padding: 98px 0 0 0;
			&-item {
				width: 207px;
				margin: 0 50px 0 0;
				&:last-child {
					margin: 0 0px 0px 10px;
				}
				h4 {
					font-weight: 700;
					font-size: 14px;
					line-height: 9px;
					letter-spacing: -0.006em;
					@include mainColor;
					margin-bottom: 50px;
				}
				p {
					font-size: 14px;
					line-height: 25px;
					letter-spacing: -0.006em;
					color: rgba(34, 53, 111, 0.5);
				}
				&-tel {
					margin: 35px 0 0 0;
					a {
						color: rgba(34, 53, 111, 0.5);
						text-decoration: none;
						&:hover {
							text-decoration: underline;
						}
					}
				}
				&-email {
					a {
						color: #006dff;
						text-decoration: none;
						&:hover {
							text-decoration: underline;
						}
					}
				}
				@media (max-width: 1200px) {
					width: 200px;
					margin: 0 30px 20px 10px;
				}
			}
			@media (max-width: 1200px) {
				width: 50%;
			}
			@media (max-width: 992px) {
				width: 100%;
				justify-content: center;
				padding: 15px;
			}
			@media (max-width: 768px) {
				justify-content: start;
			}
			@media (max-width: 576px) {
				justify-content: space-between;
			}
		}
	}
	&-licency {
		display: flex;
		align-items: center;
		background: #62d0df;
		padding: 30px 0 25px 190px;
		&-icon {
			position: relative;
			bottom: 1px;
			left: 0;
		}
		h3 {
			position: relative;
			padding: 0 0 0 10px;
			font-weight: 700;
			font-size: 14px;
			line-height: 9px;
			letter-spacing: -0.006em;
			color: #ffffff;
		}
		@media (max-width: 1200px) {
			padding: 30px 0 25px 60px;
		}
		@media (max-width: 992px) {
			padding: 30px 0 25px 30px;
		}
	}
}
