.about-us {
	display: flex;
	padding: 152px 0 75px 105px;
	&__title {
		font-size: 16px;
		line-height: 24px;
		letter-spacing: 0.06em;
		text-transform: uppercase;
		@include mainColor;
		margin-right: 225px;
		width: 93px;
		@media (max-width: 992px) {
			margin-right: 70px;
		}
		@media (max-width: 576px) {
			margin-right: 10px;
			width: unset;
		}
	}
	&__text {
		max-width: 590px;
		font-weight: 500;
		font-size: 24px;
		line-height: 29px;
		letter-spacing: 0.005em;
		@include mainColor;
		@media (max-width: 576px) {
			font-size: 20px;
		}
	}
	@media (max-width: 992px) {
		padding: 70px 10px 50px 70px;
	}
	@media (max-width: 576px) {
		padding: 15px;
	}
}
