.contact {
	display: flex;
	justify-content: space-between;
	background: #22356f;
	.column-one {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 100px 0 40px 180px;
		h3 {
			font-weight: 400;
			font-size: 16px;
			line-height: 13px;
			letter-spacing: 0.06em;
			color: #ffffff;
		}
		h4 {
			font-weight: 700;
			font-size: 64px;
			line-height: 80px;
			letter-spacing: -0.01em;
			color: #ffffff;
			@media (max-width: 1200px) {
				font-size: 55px;
			}
			@media (max-width: 992px) {
				font-size: 45px;
			}
			@media (max-width: 576px) {
				font-size: 35px;
			}
		}
		&-text {
			width: 580px;
			font-size: 36px;
			line-height: 56px;
			letter-spacing: -0.005em;
			color: #ffffff;
			@media (max-width: 1200px) {
				width: 100%;
			}
			@media (max-width: 768px) {
				margin-bottom: 20px;
			}
			@media (max-width: 576px) {
				font-size: 30px;
				line-height: 45px;
			}
		}
		&-link {
			font-weight: 700;
			font-size: 16px;
			line-height: 20px;
			letter-spacing: 0.1em;
			a {
				color: #62d0df;
				text-decoration: none;
				&:hover {
					text-decoration: underline;
				}
			}
		}
		@media (max-width: 1200px) {
			width: 50%;
			padding: 100px 15px 40px 70px;
		}
		@media (max-width: 992px) {
			width: 75%;
			padding: 20px;
		}
		@media (max-width: 768px) {
			width: 100%;
		}
	}
	.column-two {
		width: 480px;
		height: 800px;
		background: url(../img/man.jpg) 0 0 / cover no-repeat;
		@media (max-width: 768px) {
			display: none;
		}
	}
}
