.header {
	.container {
		@include container;
		padding: 25px 132px 75px 179px;
		background: url("../img/backgroundFirstBlock.jpg") 0 0 / cover no-repeat;
	}
	.hamburger {
		display: none;
	}
	.navigation {
		display: flex;
		justify-content: space-between;
		align-items: center;
		&__logo {
			font-weight: 700;
			font-size: 24px;
			line-height: 23px;
			letter-spacing: -0.035em;
			color: #62d0df;
		}
		&__list {
			max-width: 233px;
			padding: 0;
			display: flex;
			list-style: none;
			:last-child {
				margin: 0;
			}
			&-item {
				margin-right: 25px;
				a {
					font-weight: 500;
					font-size: 14px;
					line-height: 13px;
					color: #22356f;
					text-decoration: none;
				}
			}
		}
		&__btn {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 150px;
			height: 50px;
			background: #62d0df;
			border-radius: 50px;
			a {
				font-weight: 700;
				font-size: 14px;
				line-height: 14px;
				color: #ffffff;
				text-decoration: none;
			}
		}
		&-mobile {
			display: none;
		}
	}
	.description {
		max-width: 469px;
		margin-top: 164px;
		&__logo {
			font-weight: 700;
			font-size: 24px;
			line-height: 23px;
			letter-spacing: -0.035em;
			color: #62d0df;
		}
		&__slogan {
			font-weight: 700;
			font-size: 48px;
			line-height: 45px;
			letter-spacing: -0.035em;
			@include mainColor;
			margin-top: 45px;
		}
		&__text {
			font-size: 24px;
			line-height: 45px;
			@include mainColor;
			margin-top: 105px;
		}
		&__btn {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 150px;
			height: 50px;
			background: #f9b640;
			margin-top: 65px;
			a {
				font-weight: 700;
				font-size: 16px;
				line-height: 20px;
				display: flex;
				align-items: center;
				letter-spacing: 0.1em;
				color: #ffffff;
				text-decoration: none;
			}
		}
	}
	@media (max-width: 1200px) {
		.container {
			max-width: 960px;
			background: url("img/backgroundFirstBlock.jpg") 0 0 / auto 100% no-repeat;
			.description {
				max-width: 600px;
				margin-top: 50px;
				&__text {
					margin-top: 25px;
				}
				&__btn {
					margin-top: 35px;
				}
			}
		}
	}
	@media (max-width: 992px) {
		.container {
			max-width: 720px;
			padding: 15px 10px 15px 10px;
			.navigation {
				&__btn {
					width: 115px;
					height: 40px;
				}
			}
			.description {
				&__logo {
					display: none;
				}
				&__slogan {
					font-size: 40px;
					line-height: 40px;
				}
			}
		}
	}
	@media (max-width: 768px) {
		.container {
			max-width: 540px;
			.description {
				max-width: 100%;
				margin-top: 15px;
				&__slogan {
					font-size: 35px;
					line-height: 35px;
				}
				&__text {
					font-size: 21px;
					line-height: 32px;
				}
				&__btn {
					margin-top: 20px;
					width: 125px;
					height: 40px;
				}
			}
		}
	}
	@media (max-width: 576px) {
		.container {
			max-width: 100%;
			padding: 15px;
			.navigation {
				flex-wrap: wrap;
				&__list {
					margin-top: 10px;
				}
				&__btn {
					margin-top: 10px;
				}
			}
			.description {
				&__slogan {
					font-size: 30px;
				}
				&__text {
					font-size: 20px;
				}
			}
		}
	}
}
