.help {
	padding: 70px 0 0 0;
	:last-child {
		margin-bottom: 40px;
		@media (max-width: 576px) {
			margin-bottom: 20px;
		}
	}
	&__title {
		margin-bottom: 24px;
		font-size: 16px;
		line-height: 20px;
		text-align: center;
		letter-spacing: 0.06em;
		color: rgba(34, 53, 111, 0.5);
	}

	&__cards {
		display: flex;
		flex-flow: row wrap;
		margin-bottom: 120px;
		@media (max-width: 1200px) {
			justify-content: center;
		}
		@media (max-width: 576px) {
			margin-bottom: 10px;
		}
		&-item {
			display: flex;
			flex-direction: column;
			width: 480px;
			height: 480px;
			background: red;
			padding: 96px 0 0 54px;
			@media (max-width: 576px) {
				width: 100%;
			}
			.divider {
				width: 255px;
				height: 1px;
				background: #ffffff;
				&-big {
					width: 340px;
					@media (max-width: 576px) {
						width: 80%;
					}
				}
			}
			&-title {
				position: relative;
				font-weight: bold;
				font-size: 32px;
				line-height: 29px;
				letter-spacing: -0.006em;
				color: #ffffff;
				margin: 23px 0 0 25px;
				&::before {
					content: "";
					display: inline-block;
					position: absolute;
					top: 5px;
					left: -25px;
					width: 9px;
					height: 9px;
					background-color: #e9b730;
					border-radius: 50%;
				}
			}
			&_small {
				width: 360px;
				height: 360px;
			}
			&-text {
				width: 480px;
				padding: 0 75px 35px 105px;
				@media (max-width: 576px) {
					width: 100%;
					padding: 45px;
				}
				&-title {
					font-size: 28px;
					line-height: 35px;
					letter-spacing: -0.03em;
					@include mainColor;
					margin: 0 0 25px 0;
				}
				&-description {
					font-size: 16px;
					line-height: 27px;
					letter-spacing: -0.03em;
					color: rgba(34, 53, 111, 0.5);
				}
			}
		}
	}
}
