.info {
	display: flex;
	&-column-one {
		width: 480px;
		height: 800px;
		background: url(../img/meeting.jpg) 0 0 / auto 100% no-repeat;
		@media (max-width: 992px) {
			width: 50%;
		}
		@media (max-width: 768px) {
			display: none;
		}
	}
	&-column-two {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 103px 0 42px 106px;
		&-title {
			font-size: 16px;
			font-weight: 400;
			line-height: 13px;
			letter-spacing: 0.06em;
			text-transform: uppercase;
			@include mainColor;
			@media (max-width: 992px) {
				margin-bottom: 30px;
			}
		}
		&-text {
			max-width: 676px;
			font-weight: 700;
			font-size: 36px;
			line-height: 45px;
			letter-spacing: -0.01em;
			@include mainColor;
			@media (max-width: 992px) {
				margin-bottom: 30px;
			}
			@media (max-width: 768px) {
				font-size: 30px;
			}
			@media (max-width: 576px) {
				font-size: 25px;
			}
		}
		&-link {
			font-weight: 700;
			font-size: 16px;
			line-height: 20px;
			letter-spacing: 0.1em;
			&:hover {
				text-decoration: underline;
			}
			a {
				text-decoration: none;
				color: #fab63e;
			}
		}
		@media (max-width: 992px) {
			width: 50%;
			padding: 20px;
			justify-content: start;
		}
		@media (max-width: 768px) {
			width: 100%;
		}
	}
}
